body,html {
  overflow: hidden;
  height: 100%;
}

@font-face {
  font-family: mplus_hzk_12;
  src: url(./mplus_hzk_12.ttf);
}

body {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: mplus_hzk_12, 'monospace';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: gray;
}

