.App {

  background-color: #ffebc9;
  width: 1024px;
  height: 768px;
  display: grid;
  padding: 4px;
  grid-template-columns: 160px 532px 128px;
  grid-template-rows: 64px 536px 64px;
  color: white;
}

.PlayersInfo {

  grid-column: 1/2;
  grid-row: 2/3;
}

.DrawPad128 {
  grid-row: 2/3;
  grid-column: 2/3;
}

.ToolBar {
  margin-top: 16px;

  grid-row: 3/4;
  grid-column: 2/3;
}

.Console {
  grid-row: 2/3;
  grid-column: 3/4;
  margin-top: 16px;
}

.Border {
  grid-row: 1/4;
  grid-column: 1/4;

  width: 1056px;
  height: 800px;
  z-index: -1;
  background-color: black;
}

