.Output {
    height: 500px;
    width: 100%;
    background-color: #222222;
    overflow-y: scroll;
    color: whitesmoke;
}

.InputArea>input {
    position: relative;
    font-size: 12px;
    font-family: mplus_hzk_12;

    width: 100%;
    height: 1em+4px;

    background-color: black;
    color: whitesmoke;
    bottom: 0;

    border: none;
    overflow: hidden;
    box-sizing: border-box;
    outline: none;
}

.Console {
    font-size: 12px;
    font-family: mplus_hzk_12;

    image-rendering: pixelated;
    margin-left: 20px;
    background-color: gray;
    height: 512px;
    width: 24em;
}

