.ToolBar {
    display: grid;
    grid-gap: 16px;
    grid-template-columns: 64px 2fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 64px;
    image-rendering: pixelated;

}
.ColorContainer {
    grid-column: 1/2;
    position: relative;
    width: 64px;
    height: 64px;
}

.ColorContainer>#color-block {
    position: absolute;
    top: 4px;
    left: 4px;
    width: 52px;
    height: 52px;
    padding: 0px;
    border-style: hidden;
    z-index: 0;
}

.ColorContainer>input[type=color] {
    display: none;
}

.ColorContainer>#color-picker-border {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 64px;
    height: 64px;

    padding: 0px;

    background-repeat: no-repeat;
    background-size: cover;
    background-image: url(./color_container.webp);
    z-index: 1;
}

.Palette {
    grid-column: 2/3;
    position: relative;
}
.PaletteBlock {
    position: relative;
    width: 32px;
    height: 32px;
    background-repeat: no-repeat;
    background-size: cover;
}

.PaletteBlockColor {
    position: absolute;
    top: 4px;
    left: 4px;
    z-index: 0;
    width: 20px;
    height: 20px;
}

.PaletteBlockBorder {
    position: absolute;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url(./pallete_block.webp);
    
    z-index: 1;
    width: 32px;
    height: 32px;
}

.Timer {
    color: black;
    font-family: mplus_hzk_12;
    font-size: 48px;
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.ColorPickerButton {
    grid-column: 4/5;
    grid-row: 1;
    background-repeat: no-repeat;
    background-size: cover;
    width: 64px;
    height: 64px;
    background-image: url(./picker_button.webp);

}

.PencilButton {
    grid-column: 5/6;
    grid-row: 1;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url(./pencil_button.webp);
    width: 64px;
    height: 64px;

}

.EraserButton {
    grid-column: 6/7;
    grid-row: 1;
    background-repeat: no-repeat;
    background-size: cover;
    width: 64px;
    height: 64px;
    background-image: url(./eraser_button.webp);
}