.LexiconUploader>button,input {
    font-family: mplus_hzk_12;
    font-size: 12px;
    box-sizing: content-box;
}


.Marker {
    display: grid;
    grid-template-rows: repeat(12px);
}


.Poll {
    display: grid;
}

.PollOption>button {
    text-align: left;
    border: none;
    font-family: mplus_hzk_12;
    font-size: 12px;
    width: 100%;
    background-color: #404040;
}

.PollOption>button:hover {
    background-color: #808080;
}

.Notice {
    background-color: #ffdd99;
    color: #444
}

.LocalInfo {
    text-align: center;
    color: #ffc966;
}