#PlayerAvatar0.PlayerAvatarOnline {
    filter: hue-rotate(305deg);
}

#PlayerAvatar1.PlayerAvatarOnline {
    filter: hue-rotate(0deg);
}

#PlayerAvatar2.PlayerAvatarOnline {
    filter: hue-rotate(45deg);
}

#PlayerAvatar3.PlayerAvatarOnline {
    filter: hue-rotate(90deg);
}

#PlayerAvatar4.PlayerAvatarOnline {
    filter: hue-rotate(135deg);
}

#PlayerAvatar5.PlayerAvatarOnline {
    filter: hue-rotate(180deg);
}

#PlayerAvatar6.PlayerAvatarOnline {
    filter: hue-rotate(225deg);
}

#PlayerAvatar7.PlayerAvatarOnline {
    filter: hue-rotate(270deg);
}

.PlayerAvatarUnready {
    background-image: url(./bubble_sleep.webp);
}

.PlayerAvatarReady {
    background-image: url(./bubble.webp);
}

.PlayerAvatarOffline {
    filter: grayscale(100%);
}

.PlayerInfo {
    display: grid;
}

.PlayerItem {
    margin-bottom: 20px;
    margin-right: 20px;
    color: black; 
    display: grid;
    column-gap: 8px;
    grid-template-columns: 16px, 40px, 60px;
    grid-template-rows: 10px, 10px, 10px, 10px;
    
}

.PlayerPointer {
    width: 16px;
    height: 20px;
    grid-column: 0/1;
    grid-row: 2/4;
    
    image-rendering: pixelated;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url(./pointer.webp);
}

.PlayerAvatar {
    width: 40px;
    height: 40px;
    grid-column: 2/3;
    grid-row: 1/5;

    image-rendering: pixelated;
    background-repeat: no-repeat;
    background-size: cover;
}


.PlayerName {
    width: 60px;
    height: 20px;
    font-size: 12px;
    text-align: left;
    grid-column: 3/4;
    grid-row: 2/3;
}

.ScoreBoard {
    font-size: 12px;
    height: 20px;
    grid-column: 3/4;
    grid-row: 3/5;
}

.ScoreVoteDown {
    color: #ff7777;
}

.ScoreVoteNeutral  {
    color: #22ff22;
}

.ScoreVoteUp {
    color: #22bbff;
}